import "./styles.css";
import displayHome from "./home.js";
import displayMenu from "./menu.js";
import displayContact from "./contact.js";
import icon from "./img/fish.png";

//<a href="https://www.flaticon.com/free-icons/fish" title="fish icons">Fish icons created by Cuputo - Flaticon</a> footer attribute

if(module.hot){
    module.hot.accept("./styles.css", function(){
        console.log("Applying updated css.");
    });
}

const nav = document.querySelector("nav");
const leftFishIcon = new Image(); const rightFishIcon = new Image();
leftFishIcon.src = icon; rightFishIcon.src = icon;
nav.insertBefore(leftFishIcon, nav.querySelector(".home"));
nav.appendChild(rightFishIcon);

const content = document.querySelector("#content");

document.addEventListener("click", (e) => {
    if(e.target.classList.contains("home")){
        clearPage();
        displayHome();
    }
    if(e.target.classList.contains("menu")){
        clearPage();
        displayMenu();
    }
    if(e.target.classList.contains("contact")){
        clearPage();
        displayContact();
    }
});

function clearPage(){
    while(content.firstChild){
        content.removeChild(content.firstChild);
    }
}

export default content;

displayHome();


